.footer-main {
    font-weight: 600;
}

#fotter-background {
    background-color: #042c66; /* For browsers that do not support gradients */
    background-image: -webkit-gradient(linear, left top, right top, from(#042c66), to(#272c33));
    background-image: -webkit-linear-gradient(left, #042c66, #272c33);
    background-image: -o-linear-gradient(left, #042c66, #272c33);
    background-image: linear-gradient(to right, #042c66, #272c33); /* Standard */
}

.btn-floating.btn-sm i, .btn-floating.wishlist i, .btn-floating.btn-small i {
    font-size: 0.96154rem;
    line-height: 36.15385px;
    margin-right: 12px;
}


